<template>
  <div class="page page-rich_text">
    <div class="content">
      <div v-html="content"></div>
    </div>
  </div>
</template>

<script>
import { getCarouselChartDetail } from "@/apis/user";
export default {
  name: "rich_text",
  data() {
    return {
      content: "",
    };
  },
  computed: {
    fn() {
      if (this.$route.params.type === "carousel") return "getCarouselChartDetail";
    },
    title() {
      if (this.$route.params.type === "carousel") return "轮播图详情";
    },
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
    this.$root.$emit("root:set-title", this.title);
    this[this.fn]();
  },
  methods: {
    async getCarouselChartDetail() {
      const { ok, data } = await getCarouselChartDetail({ id: this.$route.params.id });
      if (ok) {
        this.content = data.content;
        this.$root.$emit("root:set-title", data.title);
      }
    },
  },
};
</script>

<style lang="scss">
.page.page-rich_text {
  overflow-y: auto;
  .content {
    padding: 10px 20px;
  }
}
</style>
